
/**
 * Returning Type
 *
 * @export
 * @enum {number}
 */
export enum ReturningType {
    /**
     * it is a sync returning
     */
    sync = 'sync',

    /**
     * it is promise asyce returning
     */
    promise = 'promise',
    /**
     * it is observable asyce returning
     */
    observable = 'observable'
}
